.header {
  margin-bottom: 50px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0;
  margin-bottom: 100vh; }

.hero__content {
  z-index: 5;
  position: relative;
  margin-top: 500px;
  width: 450px;
  height: 450px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  text-align: center;
  align-items: center;
  margin: 0 auto;
  border-radius: 50%;
  background-color: #ffffff; }

#video-bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  clip-path: circle(50%);
  min-width: 414px;
  min-height: 414px;
  max-width: 1024px;
  max-height: 1024px;
  margin: 0 auto;
  z-index: 0;
  background: url(../../../assets/back.png) no-repeat #94a233;
  background-size: cover; }

#video-bg > video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

@supports (object-fit: cover) {
  #video-bg > video {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; } }

.hero__logo {
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
  padding-top: 30px; }

.hero__slogan {
  color: #ffffff;
  font-weight: 400;
  font-size: 4.5rem;
  line-height: 98px;
  margin-top: 80px;
  text-align: center;
  line-height: 6.128rem;
  display: flex;
  flex-direction: column; }
  .hero__slogan .slogan__line {
    background: #000;
    display: inline-block; }
  .hero__slogan .slogan__line-top {
    white-space: nowrap;
    display: inline-block;
    word-wrap: normal;
    text-align: center; }

.hero__arrow {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 40px;
  cursor: pointer; }

@media (max-width: 850px) {
  .hero__slogan {
    font-size: 2rem;
    line-height: 2.724rem; }
  .slogan__line-top {
    white-space: normal;
    min-width: 414px; }
  .hero__logo {
    font-size: 1.125rem; }
  .hero__content {
    z-index: 5;
    position: relative;
    margin-top: 500px;
    width: 414px;
    height: 414px;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    text-align: center;
    align-items: center;
    margin: 0 auto;
    border-radius: 50%;
    background-color: #ffffff; } }

.navigation {
  background-color: #000000;
  color: #ffffff;
  position: sticky;
  top: 0;
  overflow: hidden;
  z-index: 5; }

.navigation__wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.navigation__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }
  .navigation__list li {
    margin: 38px 25px 38px 0;
    font-size: 1.125rem;
    font-weight: normal;
    text-transform: uppercase; }

.mobile__menu {
  width: 32px;
  height: 32px;
  display: none; }

@media (max-width: 650px) {
  .navigation__list {
    display: none; }
  .mobile__menu {
    display: inline-block;
    margin-right: 15px; }
  .navigation__wrap {
    align-items: center; }
  .navigation__logo {
    margin-left: 15px; } }

.about {
  margin-top: 250px; }

.about__row {
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: 49% 49%; }

.about__title {
  font-size: 3rem;
  font-weight: 600px;
  font-family: "Noto Serif JP", serif; }

.about__text {
  text-align: left;
  line-height: 1.75rem;
  font-size: 1.125rem;
  align-content: center; }

.about__text-center {
  text-align: right;
  display: flex;
  align-items: center;
  margin-right: 30px; }

.text__item {
  overflow: hidden; }

._anim-show {
  transform: translate(0, 120%);
  transition: all 1.2s ease 0s;
  opacity: 0; }

._anim-show.-active,
.-active ._anim-show {
  transform: translate(0, 0);
  opacity: 1; }

@media (max-width: 715px) {
  .about__row {
    margin: 0 15px 50px 15px;
    display: grid;
    grid-template-columns: 99%;
    grid-template-rows: 49% 49%; }
  .about__img {
    order: 1; }
  .about__text-center {
    order: 2; }
  .about {
    margin-top: 40px; } }

